<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"> 
                <h4>Applicant: {{name}}</h4>
            </li>
            <li class="collection-item">Applicant id : {{applicant_id}}</li>
            <li class="collection-item">Apply For Job: {{job_apply}}</li>
            <li class="collection-item">Last Job/Org: {{last_job}}</li>
            <!-- <li class="collection-item">last qualification {{last_qualification}}</li> -->
            <li class="collection-item"><div class="chip"> Status:  {{status}} </div></li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        &nbsp;
        <button  class="btn red" ml-2> Delete </button>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-employee',
    applicant_id: '',
    job_apply: '',
    last_job: '',
    status: '',
    data () {
        return {
            id: null,
            applicant_id: null,
            name: '',
            job_apply: null,
            last_job: null,
            status:null
        }
    } ,
    beforeRouteEnter(to, from, next) {
        db.collection('applicants').where('applicant_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.applicant_id = doc.data().applicant_id
                    vm.name = doc.data().name
                    vm.job_apply = doc.data().job_apply
                    vm.last_job = doc.data().last_job
                    vm.status = doc.data().status
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    } ,
    methods: {
        fetchData () {
            db.collection('applicants').where
            ('applicant_id', '==', 
            this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.applicant_id = doc.data().applicant_id 
                        this.name = doc.data().name
                        this.job_apply = doc.data().job_apply
                        this.last_job = doc.data().last_job
                        // this.last_qualification = doc.data().last_qualification
                        this.status = doc.data().status

                    })
                })
        }
    }
}
</script>